.App {
  text-align: center;
}
.center_div{
  width: 35%;
  height: 70vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 20px -5px black;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main_div{
  width: 100%;
  height: 100vh;
  background-color: #badc58;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
h1{
  top: -30%;
  padding: 20px 0;
  color: snow;
  width: 100%;
  background: transparent;
  background-color: #6761d9;
}
h2{
  font-size: 30px;
  color: snow;
  background: transparent;
  background-color: #6761d9;
  padding: 10px 0 2px 0;
  margin-bottom: 30px;
  width: 100%;
  box-shadow: 5px 5px 15px -5px black;
}
button{
  min-height: 40px;
  border-radius: 5%;
  border-color: transparent;
  background-color: #6761d9;
  color: snow;
  font-size: 20px;
  border: none;
  padding: 2px 15px;
  margin-top: 30px;
  box-shadow: 5px 5px 15px -5px  black;

}
button:hover{
  background-color: #22a6b3;
  transform: scaleX(50x);
  
}
.btn_div{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}